footer {
  color: #fff;
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 10px 0;
  box-sizing: border-box;
  background: #161c2b;

  .footer-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .soc-icon:hover {
    background: blue;
    border-radius: 2px;
    transition: 0.4s;
  }
}
