#header {
  position: absolute;
  padding-top: 10px;
  box-sizing: border-box;
  top: 0;
  z-index: 1000;
  width: 100%;

  .logo {
    width: 150px;
    img {
      width: 100%;
    }
  }
  .wrapper-nav-select {
    display: flex;
  }
  .header-navbar {
    width: 500px;
    display: flex;
    justify-content: space-between;
    a {
      color: #fff;
    }
  }
  #languages {
    height: 20px;
    margin-left: 30px;
  }
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .mobile-header-nav {
    display: none;
  }
  .mobile-menu-button{
    color: #fff;
  }

  @media screen and (max-width: 700px) {
    .header-container {
      justify-content: space-between;
      flex-direction: row-reverse;
    }
    .logo {
      width: 100px;
    }
    .mobile-header-nav {
      display: block;
    }
    .wrapper-nav-select {
      display: none;
    }
  }
  @media screen and (max-width: 360px) {
    .logo {
      width: 50px;
    }
    .header-container {
      align-items: stretch;
    }
  }
}



