.about-us-content {
  width: 100%;
  color: #000;
  .about-us-content-header-wrapper {
    background: url(http://78.46.184.51:4000/images/fon-way.jpg) no-repeat center top;
    background-size: cover;
    .about-us-content-header{
      color: #fff;
      min-height: 300px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    a {
      color: #fff;
    }


  }
  .about-us-content-main-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    p, li{
      color: #000;
    }
    .about-us-content-main {
      width: 70%;
      padding: 10px;
      box-sizing: border-box;
    }
    aside {
      .aside-slider {

      }
      .video-container {
        position: relative;
        img, embed {
          width: 100%;
        }
      }
      .youtube-icon {
        cursor: pointer;
        background: red;
        position: absolute;
        top: 42%;
        left: 43%;
      }
      h2 {
        color: #000;
      }
      width: 25%;
    }

  }
  @media screen and (max-width: 700px) {
    .about-us-content-header {

    }

    .about-us-content-main-wrapper{
      flex-direction: column;
      align-items: center;

      .about-us-content-main {
        width: 100%;
      }
      aside {
        h2 {
          text-align: center;
        }
        width: 100%;
      }
    }
  }
}
