.contact-content-wrapper {
  width: 100%;
  p, h2 {
    color: #000;
  }
  .contact-content-header-wrapper {
    background: url(http://78.46.184.51:4000/images/map.jpg) no-repeat center top;
    background-size: cover;
    .contact-content-header{
      color: #fff;
      min-height: 300px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    a {
      color: #fff;
    }


  }
  .contact-content-main-wrapper {
    padding: 20px 0;
    box-sizing: border-box;

    .adress-section, .form-wrapper {
      padding: 10px;
      box-sizing: border-box;
    }
    .adress-section {
      width: 40%;
      .got-to-right {
        text-align: right;
      }
    }
    .form-wrapper {
      width: 40%;
    }
    .contact-content-main{
      display: flex;
      justify-content: space-around;


      .form-wrapper .form{
        max-width: 300px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .filed {
          margin-bottom: 5px;
        }
      }
    }
  }
  @media screen and (max-width: 700px) {
    .contact-content-main {
      flex-direction: column;
      align-items: center;
      .adress-section , .form-wrapper {
        width: 100%;
        margin-bottom: 20px;
      }
    }
  }
}
